/* stylelint-disable */
@font-face {
  font-family: "Popins-reg";
  src: url("../fonts/../fonts/Poppins-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Popins-semb";
  src: url("../fonts/../fonts/Poppins-SemiBold.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
.burgundy-btn {
  color: #FFFFFF;
  border-radius: 8px;
  background: #691A2E;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
  transition: background-color 0.3s ease;
}
.burgundy-btn:hover {
  background: #521424;
}

.orange-btn {
  border-radius: 8px;
  background: #E1712D;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
  color: black;
  transition: background-color 0.3s ease;
}
.orange-btn:hover {
  background: #C26226;
}

.grey-btn {
  border-radius: 8px;
  color: #FFFFFF;
  background: rgba(255, 255, 255, 0.2);
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
  transition: background-color 0.3s ease;
}
.grey-btn:hover {
  background: rgba(255, 255, 255, 0.1);
}

.grey-btn-two {
  border-radius: 8px;
  color: black;
  background: #E1712D;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
  transition: background-color 0.3s ease;
}
.grey-btn-two:hover {
  background: #C26226;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*::-webkit-scrollbar {
  width: 0;
  height: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

img,
picture {
  width: auto;
  height: auto;
  max-width: 100%;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main {
  flex: 1 1 auto;
}

@media screen and (max-width: 1024px) {
  .wrapper {
    overflow: hidden;
  }
}
[class*=__container] {
  max-width: 1270px;
  margin: 0 auto;
  padding: 0 15px;
}

body {
  font-family: "Popins-reg", sans-serif;
  font-style: normal;
  font-size: 20px;
  letter-spacing: 0.02em;
  color: #111111;
  background: transparent;
}
body._lock {
  overflow: hidden;
}

.casino-providers {
  background: #FCEDF0;
  margin: 0 0 20px 0;
}
.casino-providers._onWhite {
  padding: 15px;
  border-radius: 8px;
}
@media screen and (max-width: 350px) {
  .casino-providers._onWhite {
    padding: 5px;
  }
}
.casino-providers__container {
  display: flex;
  flex-direction: column;
  padding: 0;
}
.casino-providers__list {
  display: grid;
  grid-template-columns: calc(33.333% - 10px) calc(33.333% - 10px) calc(33.333% - 10px);
  gap: 15px;
  margin: 0 0 30px 0;
}
.casino-providers__item {
  width: 100%;
  padding: 20px;
  background: #FFFFFF;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
  border-radius: 12px;
}
.casino-providers__item-image {
  margin: 0 0 10px 0;
  max-width: 162px;
}
.casino-providers__item-image img {
  border-radius: 8px;
}
.casino-providers__item-title {
  font-size: 17px;
  line-height: 26px;
  color: #111111;
  margin: 0 0 2px 0;
}
.casino-providers__item-info {
  font-size: 13px;
  line-height: 16px;
  color: #111111;
  margin: 0 0 20px 0;
}
.casino-providers__item-info span {
  font-family: "Popins-semb", sans-serif;
}
.casino-providers__item-link {
  display: inline-block;
  padding: 12px 50px 10px;
  font-size: 15px;
  line-height: 20px;
}
.casino-providers__all-link {
  align-self: center;
  font-family: "Popins-semb", sans-serif;
  display: inline-block;
  padding: 18px 70px 16px;
  font-size: 17px;
  line-height: 20px;
}

@media screen and (max-width: 1240px) {
  .casino-providers__container {
    padding: 0;
  }
  .casino-providers__list {
    grid-template-columns: calc(50% - 7.5px) calc(50% - 7.5px);
  }
}
@media screen and (max-width: 1024px) {
  .casino-providers__list {
    grid-template-columns: calc(33.333% - 10px) calc(33.333% - 10px) calc(33.333% - 10px);
    width: 100%;
  }
}
@media screen and (max-width: 830px) {
  .casino-providers__list {
    grid-template-columns: calc(50% - 7.5px) calc(50% - 7.5px);
  }
}
@media screen and (max-width: 600px) {
  .casino-providers__list {
    grid-template-columns: 100%;
    padding: 0;
    gap: 10px;
    margin: 0 0 20px 0;
  }
  .casino-providers__item {
    padding: 15px;
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto auto auto;
    column-gap: 10px;
  }
  .casino-providers__item-image {
    grid-row: 1/2;
    grid-column: 1/3;
    margin: 0 0 12px 0;
  }
  .casino-providers__item-title {
    grid-row: 2/3;
    grid-column: 1/2;
  }
  .casino-providers__item-info {
    grid-row: 3/4;
    grid-column: 1/2;
    margin: 0;
  }
  .casino-providers__item-link {
    grid-row: 2/4;
    grid-column: 2/3;
    justify-self: end;
    align-self: start;
  }
  .casino-providers__all-link {
    padding: 17px 15px 15px;
    width: 96%;
    text-align: center;
  }
}